@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

body {
  grid-template-rows: auto 1fr;
  height: 100dvh;
  display: grid;
}

:root {
  --column-number: 4;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100dvw;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav__links {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.link:not(:first-child) {
  margin-left: 20px;
}

.link__page {
  color: #060b35;
  text-transform: uppercase;
  place-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  display: grid;
  position: relative;
}

.link__page:hover, .link__page.is-active {
  color: #00acdc;
}

.link__page.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.product {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.product:hover {
  transform: scale(1.2);
}

.product > * {
  margin-bottom: 16px;
  transition: all .3s;
}

.product__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.product__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product:hover .product__title {
  color: #34568b;
}

.product__code {
  color: #616070;
  font-size: 10px;
  line-height: 14px;
}

.product__button {
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.product__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.price {
  justify-content: space-between;
  height: 18px;
  display: flex;
}

.price__text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.price__amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.reviews {
  justify-content: space-between;
  height: 16px;
  margin-bottom: 24px;
  display: flex;
}

.reviews__text {
  font-size: 10px;
  line-height: 16px;
}

.stars {
  gap: 4px;
  height: 16px;
  list-style: none;
  display: flex;
}

.stars__star {
  aspect-ratio: 1;
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.catalog {
  grid-auto-rows: auto;
  grid-template-columns: repeat(var(--column-number), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 1023px) {
  .catalog {
    --column-number: 3;
  }
}

@media (width <= 767px) {
  .catalog {
    --column-number: 2;
  }
}

@media (width <= 487px) {
  .catalog {
    --column-number: 1;
  }
}
